import { HttpClient, HttpParams } from '@angular/common/http';
import { LocalDataSource } from '../local/local.data-source';
//import { URLSearchParams } from '@angular/http/src/url_search_params';
import { of, Observable } from 'rxjs';
import { ServerSourceConf } from './server-source.conf';
import { getDeepFromObject } from '../../helpers';
import { map, catchError } from 'rxjs/operators';

export class ServerDataSource extends LocalDataSource {

  protected conf: ServerSourceConf;

  protected lastRequestCount: number = 0;

  constructor(protected http: HttpClient, conf: ServerSourceConf|{} = {}) {
    super();

    this.conf = new ServerSourceConf(conf);

    if (!this.conf.endPoint) {
      throw new Error('At least endPoint must be specified as a configuration of the server data source.');
    }
  }

  count(): number {
    return this.lastRequestCount;
  }

  getElements(): Promise<any> {
    return this.requestElements().pipe(map(res => {
      this.lastRequestCount = this.extractTotalFromResponse(res);
      this.data = this.extractDataFromResponse(res);

      return this.data;
    }), catchError(error=>of(console.log(this.handleWebError(error))))).toPromise();
  }

  /**
   * Extracts array of data from server response
   * @param res
   * @returns {any}
   */
  protected extractDataFromResponse(rawData:any): Array<any> {
    console.log(rawData, "rawData");
    let data = !!this.conf.dataKey ? getDeepFromObject(rawData, this.conf.dataKey, []) : rawData;

    if (data instanceof Array) {
      return data;
    }

    throw new Error(`Data must be an array. Please check that data extracted from the server response by the key '${this.conf.dataKey}' exists and is array.`);
  }

  /**
   * Extracts total rows count from the server response
   * Looks for the count in the heders first, then in the response body
   * @param res
   * @returns {any}
   */
  protected extractTotalFromResponse(rawData): number {    
    return getDeepFromObject(rawData, this.conf.totalKey, 0)    
  }

  protected requestElements(): Observable<any> {
    return this.http.get(this.conf.endPoint, this.createRequestOptions());
  }

  protected createRequestOptions(): { params: HttpParams } {
    let params = new HttpParams();

    params = this.addSortRequestOptions(params);
    params = this.addFilterRequestOptions(params);
    params = this.addPagerRequestOptions(params);

    return { params };
  }

  protected addSortRequestOptions(params: HttpParams): HttpParams {
  if (this.sortConf) {
    this.sortConf.forEach((fieldConf) => {
      params = params.set(this.conf.sortFieldKey, fieldConf.field);
      params = params.set(this.conf.sortDirKey, fieldConf.direction.toUpperCase());
    });
  }
  return params;
}

protected addFilterRequestOptions(params: HttpParams): HttpParams {
  if (this.filterConf  && this.filterConf.filters) {
    this.filterConf.filters.forEach((fieldConf) => {
      if (fieldConf['search']) {
        const key = this.conf.filterFieldKey.replace('#field#', fieldConf['field']);
        params = params.set(key, fieldConf['search']);
      }
    });
  }
  return params;
}

protected addPagerRequestOptions(params: HttpParams): HttpParams {
  if (this.pagingConf &&this.pagingConf.page && this.pagingConf.perPage) {
    params = params.set(this.conf.pagerPageKey, this.pagingConf.page.toString());
    params = params.set(this.conf.pagerLimitKey, this.pagingConf.perPage.toString());
  }
  return params;
}


  handleWebError(error:any){
    if(error._body){
        var eResponse = JSON.parse(error._body);
        console.log(eResponse);
        if(eResponse && eResponse.result){
            alert(eResponse.result);
            return;
        }
    }
    alert(eResponse);
  }
}